}
div#hotTitle {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  justify-content: space-around;
  padding: 0 5px;
  background: white;
}
.flexbox2 {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  width: 90%;
  position: relative;
  left: 5%;
  white-space: nowrap;
}
div.column {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 250px;
  width: 100%;
}
div#loginTitle {
  width: 383px;
  margin: 0 auto;
  font-size: 39px;
}
div.inputLine {
  height: 35px;
  margin: 0 auto;
  background: rgb(222, 222, 222);
  position: relative;
}
div.focusing {
  background: #127de7;
  transition: 500ms;
  color: white;
}
div.blueBox {
  float: left;
  margin: 0;
  display: inline-block;
  height: 100%;
  width: 75px;
  text-align: center;
  line-height: 35px;
}
input.loginInput {
  float: right;
  width: 300px;
  height: 31px;
  margin: 0;
  outline: none;
  background: white;
  border: 0;
  padding: 0;
  padding-left: 5px;
  position: relative;
  top: 2px;
  right: 3px;
  color: rgb(111, 111, 111);
}
button#loginButton {
  width: 383px;
  height: 35px;
  background: #127de7;
  color: white;
  border: 0;
  outline: none;
  margin: 0 auto;
  cursor: pointer;
  margin-bottom: 5px;
}
div#userMessage {
  font-size: 30px;
  text-align: center;
}
button#logOut {
  width: 382px;
  border: 0;
  outline: none;
  margin: 0 auto;
  height: 35px;
  cursor: pointer;
}
div#right {
  float: right;
  width: 480px;
}
div#enterpriseHot,
div#enterpriseMine,
div#enterpriseOthers {
  @include rightBorder;
}
div#enterpriseMine span,
div#enterpriseOthers span {
  margin-bottom: 5px;
  width: 33%;
}
</style>



